@import "../../utils/vars.scss";

.icon {
  display: flex;
  align-items: center;
  margin-right: 2em;
  margin-bottom: 1em;
  font-size: 20px;
  font-weight: bold;
  img {
    height: 40px;
    margin-right: 5px;
  }
  @media screen and (min-width: $phone) {
    font-size: 1.5rem;
  }
}